import React from 'react';
import PropTypes from 'prop-types';
import styles from './styles/Trbutton.module.scss';

export const TrButton = ({ id, label, primary, type, disabled, readonly, size, block, icon, iconPosition, ...props }) => {
    return (
        <button
            type={type}
            className={`${styles['button']}
                ${size !== 'medium' ? styles[size] : ''}
                ${primary ? styles['primary'] : ''}
                ${block ? styles['block'] : ''}
                ${icon ? styles['icon'] : ''}
                ${icon ? styles[icon] : ''}
                ${iconPosition ? styles[iconPosition] : ''}
            `}
            disabled={disabled}
            {...props}
        >
            {label}
        </button>
    );
};

TrButton.propTypes = {
    type: PropTypes.oneOf(['button', 'submit', 'reset']).isRequired,
    primary: PropTypes.bool,
    block: PropTypes.bool,
    disabled: PropTypes.bool,
    size: PropTypes.oneOf(['small', 'medium', 'large']),
    label: PropTypes.string.isRequired,
    icon: PropTypes.oneOf(['null', 'write', 'upload', 'download', 'send', 'required']),
    iconPosition: PropTypes.oneOf(['null', 'left', 'right']),
    onClick: PropTypes.func,
};

TrButton.defaultProps = {
    type: 'button',
    primary: false,
    block: false,
    size: 'medium',
    label: '버튼',
    icon: 'null',
    iconPosition: 'null',
    onClick: undefined,
};
